import React from "react";
import ReactDOM from "react-dom";
import { createStore } from "redux";
import { Provider } from "react-redux";
import App from "./App";
import "./index.css";
import 'bootstrap/dist/css/bootstrap.min.css'

let tabId = 0;

const reducer = function (state = { tabs: [], refreshButton: "#1f1f1f" }, action) {
  switch (action.type) {
    case "addTab":
      return {
        tabs: [
          ...state.tabs,
          {
            id: ++tabId,
            name: "newtab" + tabId + ".js",
            code: "/* This is an empty tab.*/",
          },
        ],
        refreshButton: state.refreshButton
      };
    case "removeTab":
      return {
        tabs: state.tabs.filter((tab) => tab.id !== +action.payload.id),
        refreshButton: state.refreshButton
      };
    case "changeTabName":
      return {
        tabs: state.tabs.map((tab) =>
          tab.id !== +action.payload.id
            ? tab
            : { ...tab, name: action.payload.name }
        ),
        refreshButton: state.refreshButton
      };
    case "changeTabCode":
      return {
        tabs: state.tabs.map((tab) =>
          tab.id !== +action.payload.id
            ? tab
            : { ...tab, code: action.payload.code }
        ),
        refreshButton: state.refreshButton
      };
    case "changeApplyButtonColor":
      return {
        tabs: state.tabs,
        refreshButton: action.payload.color
      }  
    default:
      return state;
  }
};

const store = createStore(
  reducer,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById("root")
);
